<template>
  <div>
    <div class="text-center" v-if="from == 'agent'">
      <b-img :src="profile.pic_path" v-if="profile.pic_path" alt="Agent profile" height="45" width="45" class="rounded-circle" />
      <p class="mt-2 mb-0 first-letter-caps">{{ profile.name }}</p>
    </div>
    <template v-if="from == 'visitor'">
      <b-button :variant="variant" class="mt-3 ml-3" @click="$emit('visitor_click', 'call_now')">{{ buttonName }}</b-button>
      <b-button variant="primary" class="ml-3 mt-3" @click="$emit('visitor_click', 'call_back')">Call Back</b-button>
    </template>
  </div>
</template>

<script>
  import call from "../../js/dialer/call";
  export default {
    props: ["from", "buttonName", "variant"],
    data() {
      return {
        profile: {},
        show_form: false,
      };
    },
    mounted: async function() {
      if (this.from != "visitor") {
        this.profile = await call.getProfile();
        if (!this.profile.pic_path) this.profile.pic_path = "/img/default_avatar.png";
      }
    },
  };
</script>
<style scoped>
  .first-letter-caps::first-letter {
    text-transform: capitalize;
  }
</style>
